const {ccclass, property} = cc._decorator;

@ccclass
export default class CompDefaultProperty extends cc.Component {
    logTimes: number = 0;

    start () {
        //时间单位是 秒
        this.schedule(this.testFunc, 1, 2, 1);
    }

    testFunc () {
        if (this.logTimes >= 2) {
            this.unschedule(this.testFunc);
            this.destroy();
            setTimeout(()=>{
                console.log(`this.comp.isValid is ${this.isValid}`);
            })
        } else {
            console.log(`test log times is ${++this.logTimes}. 
                this.comp.node is ${this.node.name}, 
                this.comp.uuid id ${this.uuid}, this.comp.enabled is ${this.enabled}, 
                this.comp.enabledInHierarchy is ${this.enabledInHierarchy},
                this.comp.isValid is ${this.isValid}`);
        }
    }
}
